import React, { Component } from 'react'
import HeroImage from '../components/HeroImage/HeroImage'
import SearchBar from '../components/SearchBar/SearchBar'
import Contents from '../components/MovieContents/MovieContents'
import LoadBtn from '../components/LoadMoreBtn/LoadMoreBtn'
import MovieThumb from '../components/MovieThumbs/MovieThumbs'
import Spinner from '../components/Spinner/Spinner'

import './Home.css'

class Home extends Component {
    state = {
        movies: [],
        displayImg: null,
        loading: false,
        currentPage: 0,
        totalPages: 0,
        queryItem: ""
    }

    componentDidMount() {
        if (localStorage.getItem("HomeState")) {
            const retrievedState = JSON.parse(localStorage.getItem("HomeState"));
            this.setState({ ...retrievedState })
        } else {
            this.setState({ loading: true });
            const MOVIE_URI = `${process.env.REACT_APP_API_URL}/movie/popular?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&page=1`;
            this.fetchMovies(MOVIE_URI);
        }
    }

    searchMovies = (queryItem) => {
        let endpoint = "";
        this.setState({
            movies: [],
            loading: true,
            queryItem
        })

        queryItem !== ""
        ? endpoint = `${process.env.REACT_APP_API_URL}/search/movie?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&query=${queryItem}`
        : endpoint = `${process.env.REACT_APP_API_URL}/movie/popular?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&page=1`

        this.fetchMovies(endpoint);
    }

    fetchMovies = (address) => {
        fetch(address).then(data => data.json())
                        .then(data => {
                            console.log(data.results);
                            this.setState({
                                movies: [...this.state.movies, ...data.results],
                                displayImg: this.state.displayImg || data.results[1],
                                loading: false,
                                currentPage: data.page,
                                totalPages: data.total_pages
                            }, () => {
                                if(this.state.queryItem === "") {
                                    localStorage.setItem("HomeState", JSON.stringify(this.state));
                                }
                            })
                        })
                        .catch(error => console.error("Error:", error))
    }
                    
    loadMore = () => {
        let endpoint = ""
        this.setState({ loading: true });
        
        if(this.state.queryItem === "") {
            endpoint = `${process.env.REACT_APP_API_URL}/movie/popular?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&page=${this.state.currentPage + 1}`
        } else {
            endpoint = `${process.env.REACT_APP_API_URL}/search/movie/?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&query=${this.state.queryItem}&page=${this.state.currentPage + 1}`
        }
        
        this.fetchMovies(endpoint);
    }

    render() {
        return(
            <div className="rmdb-home">
            {this.state.displayImg ?
            <div>
                <HeroImage
                    image={`${process.env.REACT_APP_IMAGE_BASE_URL}/${process.env.REACT_APP_BACKDROP_SIZE}${this.state.displayImg.backdrop_path}`}
                    title={this.state.displayImg.original_title}
                    text={this.state.displayImg.overview}
                    />
                <SearchBar searchquery={this.searchMovies} />
            </div>
            : null }

            <div className="rmdb-home-grid">
                <Contents
                    header={this.state.queryItem ? "Search Results" : "Popular movies"}
                    loading={this.state.loading}
                >
                    {this.state.movies.map((child, i) => {
                        return <MovieThumb
                                key={i}
                                clickable={true}
                                image={ child.poster_path
                                ? `${process.env.REACT_APP_IMAGE_BASE_URL}/${process.env.REACT_APP_POSTER_SIZE}/${child.poster_path}`
                                : "images/no_image.jpg"}
                                movieId={child.id}
                                movieName={child.original_title}
                            />
                    })
                }
                </Contents>
                {this.state.loading ? <Spinner /> : null}
                {(this.state.currentPage <= this.state.totalPages && !this.state.loading)
                    ? <LoadBtn onClick={this.loadMore} text="Load More" />
                    : null
                }
            </div>
            </div>
        )
    }
}

export default Home;