import React from 'react'

import './Actor.css'

function Actor({ actor }) {
    const POSTER_SIZE = "w154"
    return (
        <div className="rmdb-actor">
            <img src={actor.profile_path ? `${process.env.REACT_APP_IMAGE_BASE_URL}/${POSTER_SIZE}/${actor.profile_path}` : "images/no_image.jpg"} alt="actorthumb" />
            <span className="rmdb-actor-name">{actor.name}</span>
            <span className="rmdb-actor-character">{actor.character}</span>
        </div>
    )
}

export default Actor;