import React, { Component } from 'react'
import Navigation from '../components/Navigation/Navigation'
import MovieInfo from '../components/MovieInfo/MovieInfo'
import MovieInfoBar from '../components/MovieInfoBar/MovieInfoBar'
import Content from '../components/MovieContents/MovieContents'
import Actor from '../components/Actor/Actor'
import Spinner from '../components/Spinner/Spinner'

import './Movie.css'

class Movie extends Component {
    state = {
        actors: null,
        movie: null,
        directors: [],
        loading: false
    }

    componentDidMount() {
        if (localStorage.getItem(`${this.props.match.params.movieId}`)) {
            const retrievedState = JSON.parse(localStorage.getItem(`${this.props.match.params.movieId}`))
            this.setState({ ...retrievedState })
        } else {
            let endpoint = ""
            this.setState({ loading: true })
            endpoint = `${process.env.REACT_APP_API_URL}/movie/${this.props.match.params.movieId}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`

            this.fetchMovies(endpoint)
        }
    }
    
    fetchMovies = (address) => {
        fetch(address)
        .then(data => data.json())
        .then(data => {
            console.log(data);
            if (data.status_code) {
                    this.setState({ loading: false })
                } else {
                    this.setState({ movie: data }, () => {
                        const crewInfo = `${process.env.REACT_APP_API_URL}/movie/${this.props.match.params.movieId}/credits?api_key=${process.env.REACT_APP_API_KEY}`
                        fetch(crewInfo)
                            .then(result => result.json())
                            .then(result => {
                                const directors = result.crew.filter(director => director.job === "Director" )

                                this.setState({
                                    actors: result.cast,
                                    directors: directors,
                                    loading: false
                                }, () => {
                                    localStorage.setItem(`${this.props.match.params.movieId}`, JSON.stringify(this.state))
                                })
                            })
                    })
                }
            })
        .catch(error => console.error("Error:", error))
    }

    render() {
        return(
            <div className="rmdb-movie">
                {this.state.movie ?
                    <div>
                        <Navigation movie={this.props.location.movieName} />
                        <MovieInfo movie={this.state.movie} directors={this.state.directors} />
                        <MovieInfoBar time={this.state.movie.runtime} budget={this.state.movie.budget} revenue={this.state.movie.revenue} />
                    </div>
                    : null }
                {this.state.actors ?
                    <div className="rmdb-movie-grid">
                        <Content header={"Actors"}>
                            {this.state.actors.map((actor, i) => {
                                return <Actor key={i} actor={actor} />
                            })}
                        </Content>
                    </div>
                    : null
                }
                {!this.state.actors && !this.state.loading ? <h1>No movie found</h1> : null}
                {this.state.loading ? <Spinner /> : null }
            </div>
        )
    }
}

export default Movie;