import React from 'react'
import { Link } from 'react-router-dom'
import './Header.css'

export default function Header() {
    return (
        <div className="rmdb-header">
            <div className="rmdb-header-content">
                <Link to="/">
                    <img className="rmdb-logo" alt="rmdb-img" src="images/reactMovie_logo.png" />
                </Link>
                <img className="rmdb-tmdb-logo" alt="rmdb-tmdb-img" src="images/tmdb_logo.png" />
            </div>
        </div>
    )
}